import Articles from "@/components/Articles.vue";

export default {
    // name: "Home",
    components: {
        Articles
    },
    async asyncData(context){
        let posts = undefined;

        try {
            const res = await fetch(`${context.store.state.backend}/posts/`);
            posts = await res.json();
        } catch (error){
            console.log(error);
        }

        return {
            posts,
        }
    },
}
